@import url("https://fonts.google.com/?category=Handwriting&selection.family=Kaushan+Script");

.main-cont {
    position: relative; 
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    grid-template-rows: repeat(auto-fill, minmax(350px, 1fr));
    grid-gap: 35px;
  }

  .main-section{
    position: relative;
    padding: 6%;
    background:  linear-gradient(90deg, rgba(229,235,242,1) 0%, rgba(255,255,255,1) 24%, rgba(250,250,250,1) 74%, rgba(229,235,242,1) 100%);
  box-shadow: 3px 3px 10px black ;
  border-radius: 5px;
}

.main-title, .dashboard-title {
  font-size: 2.5rem;
  font-family: 'Kaushan Script', cursive;
  border: 4px solid black;
  margin-top:-1rem;
  margin-bottom: 1.25rem;
  text-align: center
}
  .logo-container {
      width: 40%;
      max-height: 50%;
      margin-left: 27%;
  }

  .logo-container > img {
    height: 100%;
  }

  .tap {
     position: absolute;
     left: 80%;
     padding: .2rem 1rem;
    background-color: orange;
    border-radius: 20%;
    text-align: center;
    padding-top: .2rem
  }
  .tap p {
  font-size: .8em;
  color: white;
  font-weight: 700;
  text-align: center;
  
}

.tap p:last-child {
  margin-top: -.2rem;
  font-size: 2rem
}


.details {
  text-align: left;
}

  .main-details h1 {
      color: black;
      font-size: 2em;
      margin-top: 10px;
      line-height: 85%;
      margin-left: auto
  }

  .main-details h3 {
      margin-bottom: 6%;
      font-size: 1.3em;
  }

  .main-details p {
      line-height: 85%;
      font-size: 1.1em;
      margin-top: -15px;
  }

  .main-bottom {
      display: flex;
      top: 2px;
      margin-top: 5px;
    }

  .main-bottom p {
      margin: 8px 0 0 0;
      border: 1px solid black;
      padding: 2px 12px;

  }

  .main-description {
    margin-top: 6%;
  }

  .main-description-small {
    height: 3em;
    font-size: 1em;
  }

  .main-description p {
    font-size: 1em;
    overflow: hidden;
  }

  .btn-readmore {
    margin-top: .5em;
    padding: 1% 2%;
  }


  .dashboard {
    margin-left: 90%
  }

  .spinner-container {
    width: 80vw;
    /* margin-left:100%; */
    margin-top:20%;
    display: block;
    justify-content: center;
  }

  .spinner {
    width: 20%;
    margin-left: 30%

  }

  .dashboard-btn {
    margin-bottom: 2rem;
  }
  
  @media screen and (max-width: 820px){
    .dashboard {
      display: block;
      margin-left: auto;
      font-size: .9em;
    }
   
  }
 
@media (max-width: 425px) {
  
.main-cont {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 35px;
}
}